import React, {useState} from "react"
import { Link } from "gatsby"

import "./navbar.scss"

import { BsList, BsX } from "react-icons/bs";

const Navbar = () => {
  const menuItem = [
    { id: "about", name: "Firma" },
    { id: "services", name: "Działalność" },
    { id: "team", name: "Zespół" },
    { id: "gallery", name: "Galeria" },
    { id: "contact", name: "Kontakt" },
    { id: "map", name: "Dojazd" },
  ]

  const [toggle, setToggle] = useState(false);

  const toggleMenuIcon = () => setToggle(!toggle);

  return (
    <nav className="nav">
      <div className="container">
        <a className="navbar-brand" href="#" aria-current="page">
          BMR Sp. z o. o.
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
          onClick={toggleMenuIcon}
        >
          {!toggle && <BsList />}
          {toggle && <BsX />}
        </button>
        <div className="collapse navbar-collapse" id="navbarContent">
          <ul className="navbar-nav">
            {menuItem.map((item, index) => 
              <li key={item.id+index} className="nav-item">
                <Link to={'#' + item.id} className={`nav-link ${index===0 ? 'active': ''}`}>
                  {item.name}
                </Link>
              </li>
            )}
          </ul>
          <div className="d-flex">
            <a href="#contact">
              <button type="button">Skontaktuj się z nami</button>
            </a>
          </div>
        </div>
      </div>
    </nav>
  )
}

export default Navbar
